<template>
  <section class="blog">
    <div class="container">
      <TheTitle :theme="theme" :title="title" />
      
      <div class="blog-wrapper">
        <blog-card
          @click="goToBlogPost(post._id)"
          v-for="post in posts" 
          :key="post.id"
          :post_data="post"
        />
      </div>
    </div>
  </section>
</template>

<script>
import TheTitle from '../components/The-title'
import BlogCard from '../components/Blog-card'
import axios from 'axios'

export default {
  name: 'Blog',
  components: {
    TheTitle,
    BlogCard
  },
  data() {
    return {
      theme: 'Main menu / Blogs',
      title: 'Blogs',
      posts: []
    }
  },
  methods: {
    goToBlogPost(id) {
      this.$router.push({ name: 'Blog-post', params: {postId: id} })
    },
    async getAllBlogs() {
      const dataRes = await axios(`http://159.65.58.38:3000/Blog`).then(res => res.data)
      this.posts = dataRes
    }
  },
  mounted() {
    this.getAllBlogs()
  },
}
</script>

<style lang="scss">
@mixin media-size($size) {
  @media (max-width: $size) {
    @content;
  }
}

.blog{
  min-height: 100vh;
    padding: 220px 0 70px;
    // Media
    @include media-size(1230px) {
        padding: 170px 0 30px;
    }
    @include media-size(790px) {
        padding: 150px 0 30px;
    }
    @include media-size(570px) {
        padding: 150px 15px 30px;
    }
    
    .blog-wrapper{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      gap: 30px;
      margin-top: 100px;
    }
}
</style>